:root {
  --mainColor: rgb(252, 252, 252);
  /* --containersBgColor: hsla(0, 0%, 0%, 0.752); */
  --containersBgColor: #faf9f9;
  --navHoverColor: hsl(124, 79%, 74%);
  /* --navBarBg: hsla(0, 0%, 0%, 0.93); */
  --navBarBg: #386641;
  --PrimaryColor: #7ae582;
}

* {
  margin: 0;
  font-family: "Gill Sans", "Gill Sans MT", Times, serif;
}

.container {
  background-color: var(--containersBgColor);
  min-height: 600px;
  border-bottom: 8px solid black;
}

.navbar {
  background-color: var(--navBarBg);
  color: var(--mainColor);
  height: 70px;
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  z-index: 2;
}
.navbar .logo {
  width: 155px;
  height: 105px;
}

.navbar .menuList {
  list-style: none;
  display: flex;
  cursor: pointer;
  /* padding: 0 280px; */
  text-decoration: none;
}

.navbar .menuList li {
  /* padding: 0 20px; */
  padding-left: 25px;
  text-decoration: none;
  /* font-size: 5rem; */
}

.navbar .menuList li a {
  text-decoration: none;
  color: var(--mainColor);
  transition: color 0.5s;
}

.navbar .menuList li a:hover {
  color: var(--navHoverColor);
}

.firstContainerBody {
  text-align: center;
  padding-top: 17%;
}

.second {
  /* width: 50px; */
  display: grid;
  align-items: center;
  grid-template-columns: repeat(2, 1fr);
}

.itemcards {
  border: 1px solid rgb(32, 32, 32);
  margin: 40px 80px;
  height: 320px;
  border-radius: 5px;
  padding: 3px 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  bottom: 2px;
}

.addbtns {
  /* display: flex; */
  /* align-self: baseline; */
  position: absolute;
  bottom: 20px;
}

.addbtns input {
  /* border: 5px solid tomato; */
  width: 50px;
  font-size: 1em;
  text-align: center;
}

.addbtns button {
  background-color: var(--navBarBg);
  color: var(--mainColor);
  border: none;
  padding: 10px;
  border-radius: 5px;
  width: 115px;
  height: 37;
  cursor: pointer;
  box-sizing: border-box;
  transition: 0.5s;
}
